const auth = {
  state: {
    password: {},
    isScroll: false,
  },
  mutations: {
    SET_PASSWORD(state, password) {
      state.password = password;
    },

    SET_ISSCROLL(state, isScroll) {
      state.isScroll = isScroll;
    },
  },
  actions: {
    ADD_PASSWORD(content, password) {
      let localPassword = JSON.parse(localStorage.getItem("password"));
      if (
        !localPassword ||
        localPassword === null ||
        localPassword === undefined
      ) {
        content.commit("SET_PASSWORD", password);
        localStorage.setItem("password", JSON.stringify(password));
      }
    },

    ADD_ISSCROLL(content, isScroll) {
      content.commit("SET_ISSCROLL", isScroll);
      localStorage.setItem("isScroll", isScroll);
    },
  },
  getters: {},
};

export default auth;
