<template>
  <div class="projectBox">
    <div class="projectBox-top">
      <div class="projectBox-top-left">工作经历</div>

      <div class="projectBox-top-right">
        <span v-for="(item, index) in companyList" :key="index">
          <el-tooltip
            class="item"
            effect="dark"
            :content="item.fullName"
            placement="bottom"
          >
            <span
              :class="chooseFlag === index ? 'choose-it' : ''"
              @click="chooseIt(item, index)"
            >
              {{ item.showName }}
            </span>
          </el-tooltip>
        </span>
      </div>
    </div>

    <div class="slide active">
      <qiYun ref="qiYun" />
    </div>

    <div class="slide">
      <tianLing ref="tianLing" />
    </div>
  </div>
</template>

<script>
import qiYun from "@/views/homePage/projectBoxUnit/qiYun";
import tianLing from "@/views/homePage/projectBoxUnit/tianLing";
import { companyList, companyListEncryption } from "@/api/apifoxInterface";

export default {
  name: "projectBox",
  components: { qiYun, tianLing },
  props: {},
  data() {
    return {
      chooseFlag: 0,
      companyList: [],
    };
  },
  methods: {
    chooseIt(item, index) {
      this.chooseFlag = index;
      // fullpage_api.moveSlideRight(); // 向右移动
      this.$parent.moveTo("index?page=2", index); // 移动到指定位置
    },

    /**
     * @Event 方法
     * @description: 自动滚动
     * */
    autoPlay() {
      this.time = setInterval(() => {
        this.$refs.page.api.moveSlideRight(); // 向右滚动
      }, 15000);
    },

    /**
     * @Event 方法
     * @description: 点击左下角的小圆点，同时更新右上角选中状态
     * */
    updateChooseFlag(index) {
      this.chooseFlag = index;
    },

    getCompanyList() {
      companyListEncryption().then((res) => {
        this.companyList = res.data;
      });
    },
  },
  created() {
    this.getCompanyList();
  },
  mounted() {},
  destroyed() {
    // clearInterval(this.time)
  },
};
</script>

