<template>
  <div class="openBox">
    <div class="openBox-top">
      <div class="openBox-top-left">开源项目</div>

      <div class="openBox-top-right"></div>
    </div>

    <div class="openBox-content">
      <el-carousel :interval="10000" type="card" arrow="always">
        <el-carousel-item v-for="(item, index) in openProjectData" :key="index">
          <div class="openBox-content-box">
            <!--            <img-->
            <!--              @click="showImgDialog(item)"-->
            <!--              :src="`/img/openProject/${item.projectName}.png` | imgFormat"-->
            <!--              alt="请刷新重新加载..."-->
            <!--            />-->
            <div @click="showImgDialog(item)">
              <imgComp
                class="imgComp"
                :src="`/img/openProject/${item.projectName}.png` | imgFormat"
              />
            </div>

            <el-tooltip
              class="item"
              effect="dark"
              :content="`点击访问${item.projectName}线上地址`"
              placement="top"
            >
              <div
                class="openBox-content-box-content"
                @click="openProjectLink(item.projectLink)"
              >
                <span class="openBox-content-box-content-name">
                  {{ item.projectName }}
                </span>
                <span class="openBox-content-box-content-info">
                  {{ item.moreInfo }}
                </span>
              </div>
            </el-tooltip>
          </div>
        </el-carousel-item>
      </el-carousel>
    </div>

    <!-- 图片预览弹窗 -->
    <div class="picBox" v-if="showPic">
      <div class="picBox-close">
        <span> {{ showPicName }} </span>
        <i @click="showPic = !showPic" class="el-icon-close"></i>
      </div>
      <!--      <img-->
      <!--        :src="`/img/openProject/${showPicName}.png` | imgFormat"-->
      <!--        alt="刷新重新加载..."-->
      <!--      />-->

      <imgComp :src="`/img/openProject/${showPicName}.png` | imgFormat" />
    </div>
    <!-- 图片预览弹窗 -->
  </div>
</template>

<script>
import { openProject } from "@/api/allInterface";

export default {
  name: "openBox",
  components: {},
  props: {},
  data() {
    return {
      showPic: false,
      showPicName: null,
      openProjectData: [],
    };
  },
  methods: {
    getOpenProjectData() {
      openProject().then((res) => {
        if (res.data.code === 200) this.openProjectData = res.data.data;
      });
    },

    showImgDialog(item) {
      this.showPic = true;
      this.showPicName = item.projectName;
    },

    openProjectLink(link) {
      window.open(link, "_blank");
    },
  },
  created() {
    this.getOpenProjectData();
  },
  mounted() {},
  destroyed() {},
};
</script>

