<template>
  <div class="friendlyLink">
    <div class="friendlyLink-outBox1">
      <div class="friendlyLink-title">关于我：</div>
      <div v-for="(value, key) in friendlyLinkObj" :key="key">
        <div class="friendlyLink-outBox1-box">
          <div class="friendlyLink-outBox1-icon">
            <i :class="`iconfont icon-${key}`"></i>
            <span>{{ key }}:</span>
          </div>

          <div class="friendlyLink-outBox1-aBox">
            <a class="friendlyLink-outBox1-a" :href="value" target="_blank">
              {{ value }}
            </a>
          </div>
        </div>
      </div>
    </div>

    <div class="friendlyLink-outBox2">
      <div class="friendlyLink-title">留言板：</div>
      <div class="friendlyLink-outBox2-form">
        <el-form
          ref="form"
          :rules="formRules"
          :model="formData"
          label-width="96px"
        >
          <el-row :gutter="24">
            <el-col :span="12">
              <el-form-item label="姓名: " prop="name">
                <el-input
                  clearable
                  size="mini"
                  v-model="formData.name"
                  placeholder=""
                >
                  <i slot="suffix" class="iconfont icon-xingming"></i>
                </el-input>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="邮箱: " prop="email">
                <el-input
                  clearable
                  size="mini"
                  v-model="formData.email"
                  placeholder=""
                >
                  <i slot="suffix" class="iconfont icon-youxiang"></i>
                </el-input>
              </el-form-item>
            </el-col>

            <el-col :span="24">
              <el-form-item label="留言内容: " prop="message">
                <el-input
                  clearable
                  size="mini"
                  v-model="formData.message"
                  placeholder=""
                  type="textarea"
                  rows="3"
                  maxlength="300"
                  show-word-limit
                >
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>

        <div class="friendlyLink-outBox2-btn">
          <el-button
            type="primary"
            size="small"
            @click="validateForm"
            v-loading.fullscreen.lock="fullscreenLoading"
          >
            发 送</el-button
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { friendlyLink } from "@/api/allInterface";

export default {
  name: "friendlyLink",
  components: {},
  props: {},
  data() {
    return {
      friendlyLinkObj: {},
      formData: {},
      formRules: {
        email: [
          { required: true, message: "请输入邮箱！", trigger: "blur" },
          {
            type: "email",
            message: "请输入正确的邮箱地址！",
            trigger: ["blur", "change"],
          },
        ],
        message: [
          { required: true, message: "请输入留言内容！", trigger: "blur" },
        ],
      },
      fullscreenLoading: false,
    };
  },
  methods: {
    getFriendlyLink() {
      friendlyLink().then((res) => {
        if (res.data.code === 200) {
          this.friendlyLinkObj = res.data.data;
        }
      });
    },

    validateForm() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.submitForm();
        } else {
          return false;
        }
      });
    },

    submitForm() {
      this.fullscreenLoading = true;
      this.$axios({
        method: "post",
        url: "https://formspree.io/f/xzblkknl",
        headers: {
          "Content-Type": "text/html; charset=utf-8",
        },
        data: this.formData,
      })
        .then((res) => {
          if (res.data.ok) {
            this.fullscreenLoading = false;
            this.$message.success("发送成功！");
            this.formData = {};
          }
        })
        .catch((e) => {
          this.fullscreenLoading = false;
          throw e;
        });
    },
  },
  created() {
    this.getFriendlyLink();
  },
  mounted() {},
};
</script>

