<template>
  <div class="welcomeBox">
    <!-- Infinity -->
    <vue-typer
      erase-style="select-all"
      caret-animation="expand"
      initial-action="typing"
      :text="vueTyperText"
      :repeat="Infinity"
      :shuffle="false"
      :pre-type-delay="36"
      :type-delay="100"
      :pre-erase-delay="2000"
      :erase-delay="500"
      :erase-on-complete="false"
    ></vue-typer>
  </div>
</template>

<script>
import { sysConfig } from "@/api/allInterface";

export default {
  name: "welcomeBox",
  components: {},
  props: {},
  data() {
    return {
      vueTyperText: [],
    };
  },
  methods: {
    getSysConfig() {
      sysConfig().then((res) => {
        if (res.data.code === 200) {
          this.vueTyperText = [];
          this.vueTyperText = [
            res.data.data.welcomeText,
            `作者：${res.data.data.author}`,
            `笔名：${res.data.data.penName}`,
          ];
        }
      });
    },
  },
  created() {
    this.getSysConfig();
  },
  mounted() {},
};
</script>

