<template>
  <div class="qiYun">
    <el-descriptions :title="qiYunData.fullName" :column="2" border>
      <el-descriptions-item
        label="在职时间"
        label-class-name="my-label"
        content-class-name="my-content"
      >
        {{ qiYunData.duration }}
      </el-descriptions-item>

      <el-descriptions-item
        label="所在岗位"
        label-class-name="my-label"
        content-class-name="my-content"
      >
        {{ qiYunData.post }}
      </el-descriptions-item>

      <el-descriptions-item
        label="工作内容"
        label-class-name="my-label"
        content-class-name="my-content"
      >
        {{ qiYunData.workAbout }}
      </el-descriptions-item>
    </el-descriptions>

    <div class="qiYun-title">项目经历</div>
    <div class="qiYun-project">
      <el-carousel
        indicator-position="outside"
        :interval="10000"
        arrow="always"
      >
        <el-carousel-item v-for="(chunk, index) in resProjectArr" :key="index">
          <div
            class="qiYun-project-public"
            v-for="item in chunk"
            :key="item.id"
          >
            <div class="img" @click="showImgDialog(item)">
              <imgComp
                class="imgComp"
                :src="`/img/qiYunProject/${item.name}.png` | imgFormat"
              />
            </div>

            <div class="info" @click="showProjectDetailDialog(item)">
              <el-tooltip
                class="item"
                effect="dark"
                :content="`点击查看${item.name}详情`"
                placement="top"
              >
                <span class="info-name">{{ item.name }}</span>
                <span class="info-time">{{ item.time }}</span>
              </el-tooltip>
            </div>
          </div>
        </el-carousel-item>
      </el-carousel>
    </div>

    <!-- 图片预览弹窗 -->
    <div class="picBox" v-if="showPic">
      <div class="picBox-close">
        <span> {{ showPicName }} </span>
        <i @click="showPic = !showPic" class="el-icon-close"></i>
      </div>
      <imgComp :src="`/img/qiYunProject/${showPicName}.png` | imgFormat" />
    </div>
    <!-- 图片预览弹窗 -->
  </div>
</template>

<script>
import { qiYun, qiYunEncryption } from "@/api/apifoxInterface";
import makeNumArr from "@/utils/makeNumArr";
export default {
  name: "qiYun",
  components: {},
  props: {},
  computed: {},
  data() {
    return {
      showPic: false,
      showPicName: null,
      qiYunData: {},
      resProjectArr: [],
    };
  },
  methods: {
    showImgDialog(item) {
      this.showPic = true;
      this.showPicName = item.name;
    },

    showProjectDetailDialog(item) {
      console.log(item);
    },

    getQiYunData() {
      qiYunEncryption().then((res) => {
        this.qiYunData = res.data;
        this.resProjectArr =
          res.data?.projectList && res.data.projectList.length > 0
            ? makeNumArr(res.data.projectList, 6)
            : [];
      });
    },
  },
  created() {
    this.getQiYunData();
  },
  mounted() {},
};
</script>

