<template>
  <el-dialog
    title="请输入解锁码"
    v-dialog-out
    append-to-body
    v-if="dialogVisible"
    :close-on-click-modal="false"
    :visible.sync="dialogVisible"
    :before-close="hideDialog"
  >
    <div class="dialog-body">
      <el-form
        ref="form"
        :model="formData"
        :rules="formRules"
        label-width="80px"
      >
        <el-form-item size="small" label="解锁码 : " prop="password">
          <el-input
            v-model="formData.password"
            :placeholder="'提示密码：' + placeholder"
          ></el-input>
        </el-form-item>
      </el-form>
    </div>

    <div slot="footer">
      <el-button size="small" type="primary" @click="validateForm"
        >确 定</el-button
      >
    </div>
  </el-dialog>
</template>

<script>
import { mapActions } from "vuex";
import { password } from "@/api/allInterface";

export default {
  name: "passwordDialog",
  components: {},
  props: {},
  data() {
    return {
      formData: {},
      formRules: {
        password: [
          { required: true, trigger: "blur", validator: this.validatePassword },
        ],
      },
      placeholder: "",
      dialogVisible: false,
      resPassword: null,
    };
  },
  methods: {
    ...mapActions(["ADD_ISSCROLL"]),

    showDialog() {
      if (localStorage.getItem("password") === null) {
        this.placeholder = this.$store.state.auth.password.randomNum;
      } else {
        this.placeholder = JSON.parse(
          localStorage.getItem("password")
        ).randomNum;
      }
      this.dialogVisible = true;
      this.getPassword();
    },

    /**
     * @Interface 接口
     * @description: 获取万能解锁密码
     * */
    getPassword() {
      password().then((res) => {
        if (res.data.code === 200) this.resPassword = res.data.data.password;
      });
    },

    hideDialog() {
      this.formData = {};
      if (
        !this.$store.state.auth.isScroll &&
        !JSON.parse(localStorage.getItem("isScroll"))
      ) {
        this.$parent.moveTo("index?page=1");
      }
      this.dialogVisible = false;
    },

    validatePassword(rule, value, callback) {
      if (value === "") {
        callback(new Error("请输入解锁码"));
      } else {
        let resNum = this.placeholder * this.placeholder + this.placeholder;
        if (
          parseFloat(value) === resNum ||
          parseFloat(value) === this.resPassword + 1
        ) {
          callback();
        } else {
          callback(new Error("解锁码不正确！"));
        }
      }
    },

    validateForm() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.submitForm();
        } else {
          return false;
        }
      });
    },

    submitForm() {
      this.$message.success("解锁成功！");
      this.ADD_ISSCROLL(true);
      this.hideDialog();
    },
  },
  created() {},
  mounted() {},
};
</script>

