// 存放 apifox 网站的相关的接口 https://app.apifox.com
import request from "@/utils/request";

let baseUrl = "https://mock.apifox.com/m1/3871863-0-default";

export function qiYun() {
  return request({
    url: baseUrl + "/qiYun",
    method: "get",
    headers: {
      isToken: true,
    },
  });
}

export function qiYunEncryption() {
  return request({
    url: baseUrl + "/qiYun/encryption",
    method: "get",
    headers: {
      isToken: true,
    },
  });
}

